/*global window, document, getSiblings ,setInterval, clearInterval,getElements,getElement,getNextSibling,getPrevSibling,setAttributes,getComputedStyle,pageDirection,console*/
/*jslint es6 */
/*=== Theme Customize ===*/
document.addEventListener('DOMContentLoaded', function () {
    'use strict';
    /*=======> Main Menu <=========*/
    var mainMenu = getElement('.main-menu');
    if (mainMenu) {
        /*=====> Sub Menus <=====*/
        var subMenus = getElements('ul li ul');
        Array.from(subMenus).forEach(element => {
            element.parentNode.classList.add('submenu');
        });

        /*=====> Sub Menus Toggle <=====*/
        var subMenusTriger = getElements('.submenu > a');
        Array.from(subMenusTriger).forEach(element => {
            element.addEventListener('click', event => {
                event.preventDefault();
                var subMenuList = getNextSibling(element, 'ul');
                slideToggle(subMenuList,200);
                element.parentNode.classList.toggle('active');
            });
        });
    }

    /*=======> Main Menu Toggle <=========*/
    var mainMenuToggle = getElement('.main-menu-toggle');
    if (mainMenuToggle) {
        mainMenuToggle.addEventListener('click', event => {
            event.preventDefault();
            mainMenu.classList.toggle('folded');
        });
    };

    /*=======> Main Menu Open <=========*/
    var mainMenuToggle = getElement('.main-menu-open');
    if (mainMenuToggle) {
        mainMenuToggle.addEventListener('click', event => {
            event.preventDefault();
            mainMenu.classList.add('opened');
        });
    };

    /*=======> Main Menu Close <=========*/
    var mainMenuToggle = getElement('.main-menu-close');
    if (mainMenuToggle) {
        mainMenuToggle.addEventListener('click', event => {
            event.preventDefault();
            mainMenu.classList.remove('opened');
        });
    };

    /*======> Config Chart <========*/
    var chartElements = getElements('.lm-statistics-chart');
    Array.from(chartElements).forEach( (element,index) => {
        let date = 'احصائيات ' +element.getAttribute('data-date'),
            users = element.getAttribute('data-users'),
            discussion = element.getAttribute('data-discussion'),
            elements = element.getAttribute('data-elements'),
            lmChartConfigs = {
                type: "bar2d",
                renderAt: 'chart-container',
                width: "100%",
                height: "200",
                //=====> Data Set via json File <=====//
                dataFormat: "json",
                dataSource: {
                    chart : {
                        caption: date,
                        baseFont: "DIN NEXT",
                        theme: "fusion",
                        paletteColors: "#60CE76,#C064EA,#2E3E5F",
                        captionFontSize: "16",
                        maxBarHeight:"25",
                        captionAlignment:"right"
                    },
                    data : [{
                        label: "المستخدمين النشطين",
                        value: users
                    }, {
                        label: "مجموع النقاشات",
                        value: discussion
                    },{
                        label: "العناصر التعليمية",
                        value: elements
                    }]
                }
            };
        /*====> Set ID for Each Element <====*/
        element.setAttribute('id', 'lm-statistics-chart-'+index)
        let idName = 'lm-statistics-chart-'+index,
            lmCharts = new FusionCharts(lmChartConfigs).render(idName);
    });
});

/*=======> Responsive Charts Height <=======*/
const responsiveChart = (chart,breakpoint) => {
    /*======> Responsive Break Points <========*/
    let smallScreen = window.matchMedia("(max-width: 640px)"),
        meduimScreen = window.matchMedia("(max-width: 980px)"),
        largeScreen = window.matchMedia("(min-width: 1280px)"),
        xLargeScreen = window.matchMedia("(min-width: 1366px)");
    /*======> Resize Chart <========*/
    if (smallScreen.matches) chart.resizeTo("100%",breakpoint.small);
    if (meduimScreen.matches) chart.resizeTo("100%",breakpoint.medium);
    if (largeScreen.matches) chart.resizeTo("100%",breakpoint.large);
    if (xLargeScreen.matches) chart.resizeTo("100%",breakpoint.xLarge);
}
